import React, { useContext, useEffect, useState } from "react";
import { GameStateContext } from "../context";
import "./user.css";

const User = ({ user }) => {
  const { gameState, io } = useContext(GameStateContext);
  const isMe = gameState.profile.login === user.login;
  const [newWord, setNewWord] = useState(user.word);
  const wordChanged = newWord !== user.word;
  useEffect(() => {
    setNewWord(user.word);
  }, [user.word]);
  return (
    <div className="user" key={user.login}>
      <div className="word-wrapper">
        {isMe ? (
          <input
            style={{ color: "red" }}
            disabled
            className="user-word"
            type="text"
            value="???"
          />
        ) : (
          <input
            disabled={isMe}
            className="user-word"
            type="text"
            onChange={e => setNewWord(e.target.value)}
            value={newWord}
          />
        )}
        <div className="user-avatar-wrapper">
          <div className="user-login">{user.login}</div>
          <img className="avatar" src={user.avatar} alt="avatar" />
        </div>
        {wordChanged ? (
          <>
            <button
              onClick={() => io.changeWord(user.login, newWord)}
              className="word-button save-word"
            >
              &#10003;
            </button>
            <button
              onClick={() => setNewWord(user.word)}
              className="word-button cancel-word"
            >
              &#10007;
            </button>
          </>
        ) : null}
      </div>
    </div>
  );
};

export default User;
