import { io } from "socket.io-client";

export class SocketIoService {
  connectTime = null;
  socket = null;
  state = null;
  constructor(setState) {
    this.setState = setState;
  }
  updateState = state => {
    this.state = state;
  };
  getUsers = () => {
    if (this.state?.users?.length > 1) {
      return this.state.users.map(({ login, avatar, word }) => {
        return {
          login,
          avatar,
          word,
        };
      });
    }
    return null;
  };
  disconnect = () => {
    this.socket.off("connect");
    this.socket.off("refreshGameInfo");
    this.socket.off("disconnect");
    this.socket.disconnect();
  };

  handleRefreshGameInfo = ({ users }) => {
    this.setState(oldValue => ({
      ...oldValue,
      users,
    }));
  };

  loginCallback = result => {
    if (result.error)
      return this.setState(oldValue => ({
        ...oldValue,
        profile: {
          ...oldValue.profile,
          login: null,
          error: result.error,
          loading: false,
        },
      }));
    this.setState(oldValue => ({
      ...oldValue,
      profile: {
        ...oldValue.profile,
        login: result.login,
        avatar: result.avatar,
        error: false,
        loading: false,
      },
    }));
  };

  changeWord = (login, word) => {
    this.socket.emit("changeWord", { login, word });
  };

  connect = (login, avatar) => {
    this.setState(oldValue => ({
      ...oldValue,
      profile: {
        ...oldValue.profile,
        login: false,
        error: false,
        loading: true,
      },
    }));
    if (!this.socket) {
      const socket = io(
        process.env.NODE_ENV === "development"
          ? "ws://localhost:8888/whoami"
          : "wss://codenames-vbl.amvera.io/whoami"
      );

      socket.on("connect", () => {
        socket.emit(
          "login",
          { login, avatar, state: this.getUsers() },
          this.loginCallback
        );
        this.setState(oldValue => ({
          ...oldValue,
          connected: true,
        }));
        this.connectTime = new Date();
        console.log("connected", this.connectTime.toLocaleTimeString());
      });

      socket.on("refreshGameInfo", data => {
        this.handleRefreshGameInfo(data);
      });
      socket.on("disconnect", () => {
        this.setState(oldValue => ({
          ...oldValue,
          connected: false,
        }));
      });
      this.socket = socket;
    } else {
      this.socket.emit(
        "login",
        { login, avatar, state: this.getUsers() },
        this.loginCallback
      );
    }
  };
}
