import React, { useContext, useEffect, useState } from "react";
import { GameStateContext } from "../context";
import { AvatarGenerator } from "../services/avatar-generator";
import Spinner from "../Spinner";
import "./login-screen.css";

const avatarGenerator = AvatarGenerator.getInstance();

const LoginScreen = () => {
  const { gameState, io } = useContext(GameStateContext);
  const [login, setLogin] = useState("");
  const [avatar, setAvatar] = useState("");
  const enter = () => {
    if (!login) return;
    io.connect(login, avatar);
  };
  const generateAvatar = () => {
    setAvatar(avatarGenerator.generateAvatar());
  };
  useEffect(() => {
    generateAvatar();
  }, []);

  return (
    <div className="login-screen">
      <div className="login-wrapper">
        <div className="avatar-wrapper" style={{ marginBottom: 0 }}>
          <img className="avatar" src={avatar} alt="avatar" />
          <button onClick={generateAvatar} className="refresh-avatar">
            ↻
          </button>
        </div>
        <input
          className="user-word"
          onKeyDown={e => {
            if (e.key === "Enter") enter();
          }}
          onChange={e => setLogin(e.target.value)}
          type="text"
          placeholder="Логин"
        />
        {gameState.profile.error && (
          <span className="login-error">{gameState.profile.error}</span>
        )}
        <button className="app-button" onClick={enter}>
          {gameState?.profile?.loading ? <Spinner /> : "Войти"}
        </button>
      </div>
    </div>
  );
};

export default LoginScreen;
